import React, { useEffect, useState } from 'react';
import '../../user/addUser/AddUser.css';
import { API_BREED_URL } from '../../../config/config';
import { useNavigate } from 'react-router-dom';
import { API_ANIMALTYPE_URL } from '../../../config/config';

const createBreedUrl = API_BREED_URL;

export default function AddBreed() {
  const navigate = useNavigate();
  const [register, setRegister] = useState({
    animalTypeId: '',
  });
  const [animalTypeOptions, setanimalTypeOptions] = useState({});
  const createAnimalType = async () => {
    fetch(createBreedUrl, {
      method: 'POST',
      headers: {
        Accept: 'application/json, text/plain, */*',
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        name: register.name,
        animalTypeId: register.animalTypeId,
      }),
    })
      .then((response) => response.json())
      .then((data) => {
        if (data.message) {
          alert(data.message);
        } else {
          alert('creado con xito');
          navigate(-1);
        }
      })
      .catch((err) => {
        alert(err);
      });
  };
  useEffect(() => {
    fetch(API_ANIMALTYPE_URL, {
      method: 'GET',
      headers: {
        Accept: 'application/json, text/plain, */*',
        'Content-Type': 'application/json',
      },
    })
      .then((response) => response.json())
      .then((data) => {
        if (data.message) {
          alert(data.message);
        } else {
          console.log(data);
          setanimalTypeOptions(data);
        }
      })
      .catch((err) => {
        alert(err);
      });
  }, []);

  const handleChange = (event) => {
    const { name, value } = event.target;
    setRegister({
      ...register,
      [name]: value,
    });
    console.log(register)
  };

  const back = () => {
    navigate(-1);
  };

  return (
    <div className='home-content'>
      <div className='content'>
        <section className='section user-section'>
          <div className='form form--add-user'>
            <div className='form_input-container--add-user'>
              <select
                name='animalTypeId'
                value={register.animalTypeId}
                onChange={handleChange}
                className='form_input--add-user'
              >
                <option value=''>Seleccionar tipo de animal</option>{' '}
                {/* Valor predeterminado */}
                {animalTypeOptions && animalTypeOptions.length > 0 ? (
                  animalTypeOptions.map((option) => (
                    <option key={option.id} value={option.id}>
                      {option.name}
                    </option>
                  ))
                ) : (
                  <option disabled>No hay tipos de animales disponibles</option>
                )}
              </select>
            </div>
            <div className='form_input-container--add-user'>
              <input
                id='name'
                className='form_input--add-user'
                type='text'
                placeholder=' '
                autoComplete='new-password'
                name='name'
                onChange={handleChange}
              />
              <div className='form_cut'></div>
              <label htmlFor='name' className='form_placeholder--add-user'>
                Escriba la raza
              </label>
            </div>
            <button
              type='text'
              className='btn_form-submit--user'
              onClick={createAnimalType}
            >
              Crear Raza
            </button>
            <button
              type='text'
              className='btn_form-submit--cancel'
              onClick={back}
            >
              Cancelar
            </button>
          </div>
        </section>
      </div>
    </div>
  );
}
