import React from 'react';
//import { useState } from 'react';
import { BiPlus } from 'react-icons/bi';

import { useNavigate } from 'react-router-dom';
import AnimalTypeCard from '../animalTypeCard/animalTypeCard';

export default function AnimalTypeList({ animalTypeList }) {
  const navigate = useNavigate();
  const addUser = () => {
    navigate('./add');
  };
  return (
    <div className='content'>
      <div className='title'>Lista de tipos de animal</div>
      <div className='container container--user'>
        <div className='user-list-title'></div>
        {animalTypeList && animalTypeList.length > 0 ? (
          animalTypeList.map((animalType) => (
            <AnimalTypeCard animalType={animalType} key={animalType.id} />
          ))
        ) : (
          <p>No hay tipos de animales para mostrar</p>
        )}
      </div>
      <button className='btn btn--add-user' onClick={addUser}>
        <BiPlus fill='white' />
      </button>
    </div>
  );
}
