import React, { useEffect, useState } from 'react';
import { BiLogOut } from 'react-icons/bi';
import { useNavigate } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { removeLoggedUser } from '../../features/users/loggedUserSlice';
import MenuList from '../general/MenuList/MenuList';
import WorkingArea from '../WorkingArea/WorkingArea';
import ClientArea from '../ClientArea/ClientArea';

export default function Home() {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const loggedUser = useSelector((state) => state.Store.loggedUser.data);

  useEffect(() => {
    if (!loggedUser) {
      navigate('/petsmile/');
      return;
    }

    document.title = 'PetSmile';
  }, [loggedUser, navigate]);

  const logout = async () => {
    if (loggedUser) {
      console.log('usuario deslogueado');
    }
    dispatch(removeLoggedUser(null));

    navigate('/');
  };

  if (!loggedUser) {
    return null;
  } else if (!loggedUser.id) {
    logout();
    return null;
  } else if (loggedUser.profile === 0) {
    return <ClientArea />;
  } else if (loggedUser.profile === 1) {
    return <WorkingArea />;
  } else {
    return (
      <div className='home-content'>
        <button className='btn--logout' onClick={() => logout()}>
          <BiLogOut fill='white' />
        </button>
        <MenuList />
        <div className='content'>
          <div className='title'>Bienvenido</div>
          <p>
            {loggedUser.name1} {loggedUser.surname1}
          </p>
        </div>
      </div>
    );
  }
}
