import React, { useState, useEffect } from 'react';
import '../user/Users.css';
import { API_PET_URL } from '../../config/config';
import MenuList from '../general/MenuList/MenuList';
import { useNavigate } from 'react-router-dom';
import Loading from '../general/Loading/Loading';
import PetList from './petList/PetList';

export default function Pet() {
  const navigate = useNavigate();
  let [dataIsLoaded, setDataIsLoaded] = useState(false);
  const [petList, setPetList] = useState();
  useEffect(() => {
    const loggedUser = JSON.parse(localStorage.getItem('loggedUser'));

    if (!loggedUser) {
      navigate('/');
    } else if (loggedUser.profile > 2) {
      console.log(loggedUser);
      alert('Usted no es admin');
      localStorage.removeItem('loggedUser');
    }
    fetch(API_PET_URL + '/'+ loggedUser.id, {
      method: 'GET',
      headers: {
        Accept: 'application/json, text/plain, */*',
        'Content-Type': 'application/json',
      },
    })
      .then((res) => res.json())
      .then((json) => {
        setPetList(json);
        setDataIsLoaded(true);
      });
  }, []);

  if (!dataIsLoaded) {
    return <Loading />;
  } else {
    return (
      <div className='home-content'>
        <MenuList />
        <PetList petList={petList} />
      </div>
    );
  }
}
