import React from 'react';
//import { useState } from 'react';

import { useNavigate } from 'react-router-dom';
import PetCard from '../petCard/PetCard';
import { BiPlus } from 'react-icons/bi';

export default function PetList({ petList }) {
  // const [userPerPage, setUserPerPage] = useState(15);
  const navigate = useNavigate();
  const addPet = () => {
    navigate('./add');
  };
  return (
    <div className='content'>
      <div className='title'>Lista de mascotas</div>
      <div className='container container--user'>
        <div className='user-list-title'></div>
        {petList && petList.length > 0 ? (
          petList.map((petList) => <PetCard pet={petList} key={petList.id} />)
        ) : (
          <p>No hay mascotas</p>
        )}
      </div>
      <button className='btn btn--add-user' onClick={addPet}>
        <BiPlus fill='white' />
      </button>
    </div>
  );
}
