import React, { useState, useEffect } from 'react';
import '../user/Users.css';
import {  API_APPOINTMENT_URL } from '../../config/config';
import MenuList from '../general/MenuList/MenuList';
import { useDispatch, useSelector } from 'react-redux';
import { initializeUserList } from '../../features/users/userSlice';
import { useNavigate } from 'react-router-dom';
import Loading from '../general/Loading/Loading';
import AppointmentList from './appointmentList/AppointmentList.jsx'

export default function Appointment() {
  const navigate = useNavigate();
  let [dataIsLoaded, setDataIsLoaded] = useState(false);
  const dispatch = useDispatch();
  const Store = useSelector((state) => state.Store);
  const [loggedUser, setLoggedUser] = useState({
    name1: '',
    surname1: '',
    profile: 0,
  });
  useEffect(() => {
    const loggedUser = JSON.parse(localStorage.getItem('loggedUser'));
    setLoggedUser(loggedUser);
    if (!loggedUser) {
      navigate('/');
    }
    fetch(API_APPOINTMENT_URL, {
      method: 'GET',
      headers: {
        Accept: 'application/json, text/plain, */*',
        'Content-Type': 'application/json',
      },
    })
      .then((res) => res.json())
      .then((json) => {
        dispatch(initializeUserList(json));
        setDataIsLoaded(true);
      });
  }, []);

  if (!dataIsLoaded) {
    return <Loading />;
  } else {
    return (
      <div className='home-content'>
        <MenuList />
        <AppointmentList appointmentList={Store.userList.data} />
      </div>
    );
  }
}
