import React, { useState, useEffect } from "react";
import "../user/Users.css";
import { API_ANIMALTYPE_URL } from "../../config/config";
import MenuList from "../general/MenuList/MenuList";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import Loading from "../general/Loading/Loading";
import AnimalTypeList from "./animaltypeList/animaltypeList.jsx";

export default function AnimalType() {
  const navigate = useNavigate();
  let [dataIsLoaded, setDataIsLoaded] = useState(false);
  const [animalTypeList, setAnimalTypeList]= useState()
 
  useEffect(() => {
    const loggedUser = JSON.parse(localStorage.getItem("loggedUser"));

    if (!loggedUser) {
      navigate("/");
    } else if (loggedUser.profile > 2) {
      console.log(loggedUser);
      alert("Usted no es admin");
      localStorage.removeItem("loggedUser");
    }
    fetch(API_ANIMALTYPE_URL, {
      method: "GET",
      headers: {
        Accept: "application/json, text/plain, */*",
        "Content-Type": "application/json",
      },
    })
      .then((res) => res.json())
      .then((json) => {
        setAnimalTypeList(json)
        setDataIsLoaded(true);
      });
  }, []);

  if (!dataIsLoaded) {
    return <Loading />;
  } else {
    return (
      <div className="home-content">
        <MenuList />
        <AnimalTypeList animalTypeList={animalTypeList} />
      </div>
    );
  }
}
