import React, { useState, useEffect } from 'react';
import '../../user/addUser/AddUser.css';
import { API_APPOINTMENT_URL, API_PET_URL } from '../../../config/config';
import { useNavigate } from 'react-router-dom';
import { DatePicker, LocalizationProvider } from '@mui/x-date-pickers';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';

const createAppointmentUrl = API_APPOINTMENT_URL;
const getPetsUrl = API_PET_URL;
const loggedUser = JSON.parse(localStorage.getItem('loggedUser'));

export default function AddAppointment() {
  const navigate = useNavigate();
  const [register, setRegister] = useState({});
  const [pets, setPets] = useState([]);
  const [selectedDate, setSelectedDate] = useState(new Date());
  const [disabledDates, setDisabledDates] = useState([]);

  useEffect(() => {
    fetchPets();
    fetchAppointments();
  }, []);

  const fetchPets = async () => {
    try {
      const response = await fetch(`${getPetsUrl}/${loggedUser.id}`);
      const data = await response.json();
      setPets(data);
    } catch (error) {
      console.error('Error al obtener las mascotas:', error);
    }
  };

  const fetchAppointments = async () => {
    try {
      const response = await fetch(
        `${API_APPOINTMENT_URL}/client/${loggedUser.id}`
      );
      const appointments = await response.json();
      const disabledDates = appointments.map(
        (appointment) => new Date(appointment.date)
      );
      console.log(disabledDates);
      setDisabledDates(disabledDates);
    } catch (error) {
      console.error('Error al obtener las citas:', error);
    }
  };

  const shouldDisableDate = (date) => {
    return disabledDates.some((disabledDate) => isSameDay(date, disabledDate));
  };

  const isSameDay = (date1, date2) => {
    if (!(date1 instanceof Date) || !(date2 instanceof Date)) {
      return false;
    }
    return (
      date1.getFullYear() === date2.getFullYear() &&
      date1.getMonth() === date2.getMonth() &&
      date1.getDate() === date2.getDate()
    );
  };

  const handleDateChange = (date) => {
    setSelectedDate(date);
  };

  const createAppointment = async () => {
    fetch(createAppointmentUrl, {
      method: 'POST',
      headers: {
        Accept: 'application/json, text/plain, */*',
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        clientId: loggedUser.id,
        date: selectedDate,
        petId: register.petId,
      }),
    })
      .then((response) => response.json())
      .then((data) => {
        if (data.message) {
          alert(data.message);
        } else {
          alert('Cita creada con xito');
          navigate(-1);
        }
      })
      .catch((err) => {
        alert(err);
      });
  };

  const handleChange = (event) => {
    event.persist();
    setRegister({
      ...register,
      [event.target.name]: event.target.value,
    });
  };

  const back = () => {
    navigate(-1);
  };

  return (
    <div className='home-content'>
      <div className='content'>
        <section className='section user-section'>
          <div className='form form--add-user'>
            <div className='form_input-container--add-user'>
              <LocalizationProvider dateAdapter={AdapterDayjs}>
                <DatePicker
                  label='Seleccionar fecha'
                  onChange={handleDateChange}
                  shouldDisableDate={shouldDisableDate}
                  views={['year', 'month', 'day']}
                />
              </LocalizationProvider>
            </div>
            <div className='form_input-container--add-user'>
              <select
                name='petId'
                value={register.petId || ''}
                onChange={handleChange}
                className='form_input--add-user'
              >
                <option value='' disabled>
                  Seleccionar mascota
                </option>
                {pets.map((pet) => (
                  <option key={pet.id} value={pet.id}>
                    {pet.name}
                  </option>
                ))}
              </select>
            </div>
            <button
              type='button'
              className='btn_form-submit--user'
              onClick={createAppointment}
            >
              Crear cita
            </button>
            <button
              type='button'
              className='btn_form-submit--cancel'
              onClick={back}
            >
              Cancelar
            </button>
          </div>
        </section>
      </div>
    </div>
  );
}
