import { useLocation, useNavigate } from 'react-router-dom';
import { API_BREED_URL } from '../../../config/config';
import Loading from '../../general/Loading/Loading';
import { useEffect, useState } from 'react';

export default function PetDetailCard() {
  let [dataIsLoaded, setDataIsLoaded] = useState(false);
  const selectedUser = useLocation();
  const user = JSON.parse(selectedUser.state.selectedUser);
  const navigate = useNavigate();
  const [breedName, setBreedName] = useState('');
  let data = { user };

  const getBreedName = () => {
    fetch(`${API_BREED_URL}/${user.breedId}`, { // Corregir la URL aqu
      method: 'GET',
      headers: {
        Accept: 'application/json, text/plain, */*',
        'Content-Type': 'application/json',
      },
    })
      .then((res) => res.json())
      .then((json) => {
        setBreedName(json.name);
        setDataIsLoaded(true);
      })
      .catch((error) => {
        console.error('Error al obtener la raza:', error);
      });
  };

  useEffect(() => {
    getBreedName();
    setDataIsLoaded(true);
  }, []);

  if (user.active === true) {
    data.user.active = 'Activo';
  } else {
    data.user.active = 'Inactivo';
  }

  const back = async () => {
    navigate(-1);
  };

  if (!dataIsLoaded) {
    return <Loading />;
  } else {
    return (
      <div className='home-content'>
        <div className='content'>
          <div className='title'>
            {data.user.name1} {data.user.surname1}
          </div>
          <div className='container container--user'>
            <section
              className='section user-section'
              style={{ flexDirection: 'column', gap: '0px' }}
            >
              <div
                className='user-list-title'
                style={{ justifyContent: 'space-between' }}
              >
                <h3>Nombre: </h3>
                <h3>{data.user.name}</h3>
              </div>
              <div
                className='user-list-title'
                style={{ justifyContent: 'space-between' }}
              >
                <h3>Estado: </h3>
                <h3>{user.active}</h3>
              </div>
              <div
                className='user-list-title'
                style={{ justifyContent: 'space-between' }}
              >
                <h3>Edad</h3>
                <h3>{user.age}</h3>
              </div>
              <div
                className='user-list-title'
                style={{ justifyContent: 'space-between' }}
              >
                <h3>Gnero: </h3>
                <h3>{user.gender}</h3>
              </div>
              <div
                className='user-list-title'
                style={{ justifyContent: 'space-between' }}
              >
                <h3>Raza: </h3>
                <h3>{breedName}</h3>
              </div>
              <div className='user-details-btn-container'>
                <button type='text' className='btn--default' onClick={back}>
                  Cancelar
                </button>
              </div>
              <button
                className='btn btn-edit-users'
                name='id'
                value={JSON.stringify(user)}
              ></button>
            </section>
          </div>
        </div>
      </div>
    );
  }
}
