import React, { useState } from 'react';
import '../../user/addUser/AddUser.css';
import { API_ANIMALTYPE_URL } from '../../../config/config';
import { useNavigate } from 'react-router-dom';

const createAnimalTypeUrl = API_ANIMALTYPE_URL;

export default function AddAnimalType() {
  const navigate = useNavigate();
  const [register, setRegister] = useState({});
  const createAnimalType = async () => {
    fetch(createAnimalTypeUrl, {
      method: 'POST',
      headers: {
        Accept: 'application/json, text/plain, */*',
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
       "name": register.name
      }),
    })
      .then((response) => response.json())
      .then((data) => {
        if (data.message) {
          alert(data.message);
        } else {
          alert('creado con xito');
          navigate(-1);
        }
      })
      .catch((err) => {
        alert(err);
      });
  };
  const handleChange = (event) => {
    event.persist();
    setRegister({
      ...register,
      [event.target.name]: event.target.value,
    });
    console.log(register);
  };

  const back = () => {
    navigate(-1);
  };

  return (
    <div className='home-content'>
      <div className='content'>
        <section className='section user-section'>
          <div className='form form--add-user'>
            <div className='form_input-container--add-user'>
              <input
                id='name'
                className='form_input--add-user'
                type='text'
                placeholder=' '
                autoComplete='new-password'
                name='name'
                onChange={handleChange}
              />
              <div className='form_cut'></div>
              <label htmlFor='name' className='form_placeholder--add-user'>
                Escriba el tipo de animal
              </label>
            </div>
            <button
              type='text'
              className='btn_form-submit--user'
              onClick={createAnimalType}
            >
              Crear Tipo de animal
            </button>
            <button
              type='text'
              className='btn_form-submit--cancel'
              onClick={back}
            >
              Cancelar
            </button>
          </div>
        </section>
      </div>
    </div>
  );
}
