import React, { useState } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { API_USER_URL } from '../../../config/config';
import { useSelector } from 'react-redux';

export default function UpdateUser() {
  const loggedUser = useSelector((state) => state.Store.loggedUser.data);
  const [register, setRegister] = useState();
  const selectedUser = useLocation();
  const navigate = useNavigate();
  const profileOptions = [
    { value: 0, label: 'Cliente' },
    { value: 1, label: 'Especialista' },
    { value: 2, label: 'Administrador' },
  ];
  const activeOptions = [
    { value: true, label: 'Habilitado' },
    { value: false, label: 'Deshabilitado' },
  ]


  const options = profileOptions.filter(
    (option) => loggedUser.profile >= option.value
  );
  const userToUpdate = JSON.parse(selectedUser.state.selectedUser);
  const updateUserUrl = API_USER_URL + '/' + userToUpdate.id;
  const handleSubmit = async () => {
    try {
      await fetch(updateUserUrl, {
        method: 'PUT',
        headers: {
          Accept: 'application/json, text/plain, */*',
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(register),
      })
        .then(navigate(-1))
        .catch((error) => {
          alert('Por favor verifique los datos:' + error);
        });
    } catch (e) {
      alert(e);
    }
  };

  const handleChange = async (e) => {
    await setRegister({
      ...register,
      [e.target.name]: e.target.value,
    });
    console.log(register);
  };
  return (
    <div className='home-content'>
      <div className='content'>
        <section className='section user-section'>
          <div className='form form--add-user'>
            <div className='form_input-container--add-user'>
              <input
                id='password'
                className='form_input--add-user'
                type='text'
                placeholder=' '
                name='password'
                onChange={handleChange}
              />
              <div className='form_cut'></div>
              <label htmlFor='password' className='form_placeholder--add-user'>
                Contrasea
              </label>
            </div>
            <div className='form_input-container--add-user'>
              <input
                id='name1'
                className='form_input--add-user'
                type='text'
                placeholder=' '
                autoComplete='new-password'
                name='name1'
                minLength={3}
                maxLength={20}
                onChange={handleChange}
              />
              <div className='form_cut'></div>
              <label htmlFor='name1' className='form_placeholder--add-user'>
                Primer Nombre
              </label>
            </div>
            <div className='form_input-container--add-user'>
              <input
                id='surname1'
                className='form_input--add-user'
                type='text'
                placeholder=' '
                name='surname1'
                onChange={handleChange}
              />
              <div className='form_cut'></div>
              <label htmlFor='surname1' className='form_placeholder--add-user'>
                Primer Apellido
              </label>
            </div>
            <div className='form_input-container--add-user'>
              <input
                id='mobile'
                className='form_input--add-user'
                type='text'
                placeholder=' '
                name='mobile'
                onChange={handleChange}
              />
              <div className='form_cut'></div>
              <label htmlFor='mobile' className='form_placeholder--add-user'>
                Telefono
              </label>
            </div>
            <div className='form_input-container--add-user'>
              <input
                id='address'
                className='form_input--add-user'
                type='text'
                placeholder=' '
                name='address'
                onChange={handleChange}
              />
              <div className='form_cut'></div>
              <label htmlFor='address' className='form_placeholder--add-user'>
                Direccion
              </label>
            </div>
            <div className='form_input-container--add-user'>
              <input
                id='email'
                className='form_input--add-user'
                type='text'
                placeholder=' '
                name='email'
                onChange={handleChange}
              />
              <div className='form_cut'></div>
              <label htmlFor='email' className='form_placeholder--add-user'>
                Email
              </label>
            </div>
            <div className='form_input-container--add-user'>
              <select
                name='profile'
                defaultValue={userToUpdate.profile}
                onChange={handleChange}
                className='form_input--add-user'
              >
                {options.map((option) => (
                  <option value={option.value}>{option.label}</option>
                ))}
              </select>
            </div>
            <div className='form_input-container--add-user'>
              <select
                name='active'
                defaultValue={userToUpdate.active}
                onChange={handleChange}
                className='form_input--add-user'
              >
                {activeOptions.map((activeOption) => (
                  <option value={activeOption.value}>{activeOption.label}</option>
                ))}
              </select>
            </div>
            <button
              type='text'
              className='btn_form-submit--user'
              onClick={handleSubmit}
            >
              Actualizar informacion
            </button>
            <button
              type='text'
              className='btn_form-submit--cancel'
              onClick={handleSubmit}
            >
              Cancelar
            </button>
          </div>
        </section>
      </div>
    </div>
  );
}
