import { useEffect, useState } from 'react';
import { BiEdit, BiSearchAlt } from 'react-icons/bi';
import { useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';

export default function AppointmentCard({ appointment }) {
  const navigate = useNavigate();
  const loggedUser = useSelector((state) => state.Store.loggedUser.data);
  const [formattedDate, setFormattedDate] = useState(''); // Aqu se inicializa el estado

  const handleClick = (event) => {
    const selectedUser = event.target.value;
    navigate('./update', { state: { selectedUser } });
  };

  useEffect(() => {
    formatDate();
  }, []);

  const formatDate = () => {
    const dateString = appointment.date; // Suponiendo que appointment.date es una cadena de fecha en formato "YYYY-MM-DD"
    const date = new Date(dateString);
    setFormattedDate(date.toLocaleDateString()); // Convertir a formato legible segn la configuracin regional del navegador
  };

  const openUserDetails = (event) => {
    const selectedUser = event.target.value;
    navigate('./userDetails', { state: { selectedUser } });
  };

  return (
    <section className='section user-section' style={{ display: 'flex' }}>
      <ul className='list user-list' style={{justifyContent:'center'}}>
        <h5>{formattedDate}</h5>
      </ul>
    </section>
  );
}
