import React, { useState } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { API_ANIMALTYPE_URL } from '../../../config/config';
import { useSelector } from 'react-redux';

export default function UpdateAnimalType() {
  const loggedUser = useSelector((state) => state.Store.loggedUser.data);
  const [register, setRegister] = useState();
  const selectedUser = useLocation();
  const navigate = useNavigate();
  const activeOptions = [
    { value: true, label: 'Habilitado' },
    { value: false, label: 'Deshabilitado' },
  ];

  const elementToUpdate = JSON.parse(selectedUser.state.selectedUser);
  const updateElementUrl = API_ANIMALTYPE_URL + '/' + elementToUpdate.id;
  const handleSubmit = async () => {
    try {
      await fetch(updateElementUrl, {
        method: 'PUT',
        headers: {
          Accept: 'application/json, text/plain, */*',
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(register),
      })
        .then(navigate(-1))
        .catch((error) => {
          alert('Por favor verifique los datos:' + error);
        });
    } catch (e) {
      alert(e);
    }
  };

  const handleChange = async (e) => {
    await setRegister({
      ...register,
      [e.target.name]: e.target.value,
    });
    console.log(register);
  };
  return (
    <div className='home-content'>
      <div className='content'>
        <section className='section user-section'>
          <div className='form form--add-user'>
            <div className='form_input-container--add-user'>
              <input
                id='name'
                className='form_input--add-user'
                type='text'
                placeholder=' '
                autoComplete='new-password'
                name='name'
                minLength={3}
                maxLength={20}
                onChange={handleChange}
              />
              <div className='form_cut'></div>
              <label htmlFor='name' className='form_placeholder--add-user'>
                Nombre
              </label>
            </div>
            <div className='form_input-container--add-user'>
              <select
                name='active'
                defaultValue={elementToUpdate.active}
                onChange={handleChange}
                className='form_input--add-user'
              >
                {activeOptions.map((activeOption) => (
                  <option value={activeOption.value}>
                    {activeOption.label}
                  </option>
                ))}
              </select>
            </div>
            <button
              type='text'
              className='btn_form-submit--user'
              onClick={handleSubmit}
            >
              Actualizar informacion
            </button>
            <button
              type='text'
              className='btn_form-submit--cancel'
              onClick={handleSubmit}
            >
              Cancelar
            </button>
          </div>
        </section>
      </div>
    </div>
  );
}
