import React, { useState, useEffect } from 'react';
import '../user/Users.css';
import { API_BREED_URL } from '../../config/config.js';
import MenuList from '../general/MenuList/MenuList.jsx';
import { useNavigate } from 'react-router-dom';
import Loading from '../general/Loading/Loading.jsx';
import BreedList from './BreedList/breedList.jsx';

export default function Breed() {
  const navigate = useNavigate();
  let [dataIsLoaded, setDataIsLoaded] = useState(false);
  const [breedList, setBreedList] = useState();

  useEffect(() => {
    const loggedUser = JSON.parse(localStorage.getItem('loggedUser'));

    if (!loggedUser) {
      navigate('/');
    } else if (loggedUser.profile > 2) {
      console.log(loggedUser);
      alert('Usted no es admin');
      localStorage.removeItem('loggedUser');
    }
    fetch(API_BREED_URL, {
      method: 'GET',
      headers: {
        Accept: 'application/json, text/plain, */*',
        'Content-Type': 'application/json',
      },
    })
      .then((res) => res.json())
      .then((json) => {
        setBreedList(json);
        setDataIsLoaded(true);
      });
  }, []);

  if (!dataIsLoaded) {
    return <Loading />;
  } else {
    return (
      <div className='home-content'>
        <MenuList />
        <BreedList breedList={breedList} />
      </div>
    );
  }
}
