import { useLocation, useNavigate } from 'react-router-dom';
import { API_SCHEDULE_URL } from '../../../config/config';
import Loading from '../../general/Loading/Loading';
import { useEffect, useState } from 'react';

export default function UserDetailsCard() {
  let [dataIsLoaded, setDataIsLoaded] = useState(false);
  const selectedUser = useLocation();
  const user = JSON.parse(selectedUser.state.selectedUser);
  const navigate = useNavigate();
  let data = { user };

  useEffect(() => {
    setDataIsLoaded(true);
  }, []);
  switch (user.profile) {
    case 0:
      data.user.profile = 'Cliente';
      break;
    case 1:
      data.user.profile = 'Especialista';
      break;
    case 2:
      data.user.profile = 'Administrador';
      break;
  }
  if (user.active === true) {
    data.user.active = 'Activo';
  } else {
    data.user.active = 'Inactivo';
  }

  const back = async () => {
    navigate(-1);
  };

  if (!dataIsLoaded) {
    return <Loading />;
  } else {
    return (
      <div className='home-content'>
        <div className='content'>
          <div className='title'>
            {data.user.name1} {data.user.surname1}
          </div>
          <div className='container container--user'>
            <section
              className='section user-section'
              style={{ flexDirection: 'column', gap: '0px' }}
            >
              <div
                className='user-list-title'
                style={{ justifyContent: 'space-between' }}
              >
                <h3>Cdula: </h3>
                <h3>{data.user.id}</h3>
              </div>
              <div
                className='user-list-title'
                style={{ justifyContent: 'space-between' }}
              >
                <h3>Perfil: </h3>
                <h3>{data.user.profile}</h3>
              </div>
              <div
                className='user-list-title'
                style={{ justifyContent: 'space-between' }}
              >
                <h3>Estado: </h3>
                <h3>{user.active}</h3>
              </div>
              <div
                className='user-list-title'
                style={{ justifyContent: 'space-between' }}
              >
                <h3>Celular</h3>
                <h3>{user.mobile}</h3>
              </div>
              <div
                className='user-list-title'
                style={{ justifyContent: 'space-between' }}
              >
                <h3>Correo: </h3>
                <h3>{user.email}</h3>
              </div>
              <div className='user-details-btn-container'>
                <button value={'pets'} className='btn--default'>
                  Ver Mascotas
                </button>

                <button value={'assigments'} className='btn--default'>
                  Ver citas
                </button>

                <button type='text' className='btn--default' onClick={back}>
                  Cancelar
                </button>
              </div>
              <button
                className='btn btn-edit-users'
                name='id'
                value={JSON.stringify(user)}
              ></button>
            </section>
          </div>
        </div>
      </div>
    );
  }
}
