import React, { useEffect, useState } from 'react';
import '../../user/addUser/AddUser.css';
import {
  API_PET_URL,
  API_ANIMALTYPE_URL,
  API_BREED_URL,
} from '../../../config/config';
import { useNavigate } from 'react-router-dom';

const createPetUrl = API_PET_URL;

export default function AddPet() {
  const loggedUser = JSON.parse(localStorage.getItem('loggedUser'));
  const navigate = useNavigate();
  const [register, setRegister] = useState({});
  const [animalTypes, setAnimalTypes] = useState([]);

  const [dataIsLoaded, setDataIsLoaded] = useState(false);
  const [breeds, setBreeds] = useState([]);
  useEffect(() => {
    getAnimalTypes(); // Llama a la funcin getAnimalTypes
  }, []);

  const getAnimalTypes = async () => {
    fetch(API_ANIMALTYPE_URL, {
      method: 'GET',
      headers: {
        Accept: 'application/json, text/plain, */*',
        'Content-Type': 'application/json',
      },
    })
      .then((res) => res.json())
      .then((json) => {
        setAnimalTypes(json);
        console.log(json);
        setDataIsLoaded(true);
      })
      .catch((error) => {
        console.error('Error al obtener los tipos de animal:', error);
      });
  };

  const getBreedsByAnimalType = async (animalTypeId) => {
    fetch(`${API_BREED_URL}/type/${animalTypeId}`, {
      method: 'GET',
      headers: {
        Accept: 'application/json, text/plain, */*',
        'Content-Type': 'application/json',
      },
    })
      .then((res) => res.json())
      .then((json) => {
        console.log(json);
        setBreeds(json);
      })
      .catch((error) => {
        console.error('Error al obtener las razas:', error);
      });
  };

  useEffect(() => {
    getBreedsByAnimalType(register.selectedAnimalType);
  }, [register.selectedAnimalType]);

  const createPet = async () => {
    fetch(createPetUrl, {
      method: 'POST',
      headers: {
        Accept: 'application/json, text/plain, */*',
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        name: register.name,
        owner: loggedUser.id,
        age: register.age,
        gender: register.gender,
        breedId: register.breedId,
        photo: register.photo,
      }),
    })
      .then((response) => response.json())
      .then((data) => {
        if (data.message) {
          alert(data.message);
        } else {
          alert('Usuario creado con xito');
          navigate(-1);
        }
      })
      .catch((err) => {
        alert(err);
      });
  };
  const handleChange = (event) => {
    event.persist();
    setRegister({
      ...register,
      [event.target.name]: event.target.value,
    });
    console.log(register);
  };

  const back = () => {
    navigate(-1);
  };

  return (
    <div className='home-content'>
      <div className='content'>
        <section className='section user-section'>
          <div className='form form--add-user'>
            <div className='form_input-container--add-user'>
              <input
                id='name'
                className='form_input--add-user'
                type='text'
                placeholder=' '
                autoComplete='new-password'
                name='name'
                onChange={handleChange}
              />
              <div className='form_cut'></div>
              <label htmlFor='name' className='form_placeholder--add-user'>
                Nombre
              </label>
            </div>
            <div className='form_input-container--add-user'>
              <input
                id='age'
                className='form_input--add-user'
                type='number'
                placeholder=' '
                name='age'
                onChange={handleChange}
              />
              <div className='form_cut'></div>
              <label htmlFor='age' className='form_placeholder--add-user'>
                Edad
              </label>
            </div>
            <div className='form_input-container--add-user'>
              <select
                name='gender'
                value={register.gender}
                onChange={handleChange}
                defaultValue=''
                className='form_input--add-user'
              >
                <option value='' disabled>
                  Seleccionar gnero de mascota
                </option>
                <option value='male'>Macho</option>
                <option value='female'>Hembra</option>
              </select>
            </div>
            <div className='form_input-container--add-user'>
              <select
                name='selectedAnimalType'
                defaultValue=''
                onChange={handleChange}
                className='form_input--add-user'
              >
                <option value='' disabled>
                  Seleccionar tipo de animal
                </option>
                {animalTypes.map((animalType) => (
                  <option key={animalType.id} value={animalType.id}>
                    {animalType.name}
                  </option>
                ))}
              </select>
            </div>
            <div className='form_input-container--add-user'>
              <select
                name='breedId'
                defaultValue=''
                onChange={handleChange}
                className='form_input--add-user'
              >
                <option value='' disabled>
                  Seleccionar Raza
                </option>
                {Array.isArray(breeds) &&
                  breeds.map((breed) => (
                    <option key={breed.id} value={breed.id}>
                      {breed.name}
                    </option>
                  ))}
              </select>
            </div>

            <button
              type='text'
              className='btn_form-submit--user'
              onClick={createPet}
            >
              Crear mascota
            </button>
            <button
              type='text'
              className='btn_form-submit--cancel'
              onClick={back}
            >
              Cancelar
            </button>
          </div>
        </section>
      </div>
    </div>
  );
}
