import { Link } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
export default function MenuList() {
  const loggedUser = useSelector((state) => state.Store.loggedUser.data);
  let isAdmin = loggedUser.profile > 1;
  let isSpecialist = loggedUser.profile > 0;
  let isClient = loggedUser.profile === 0;
  return (
    <div className='home-content'>
      <input type='checkbox' id='active' />
      <label htmlFor='active' className='menu-btn'>
        <span></span>
      </label>
      <label htmlFor='active' className='close'></label>

      <div className='wrapper'>
        <ul>
          <li>
            <Link to={'/home'}>Inicio</Link>
          </li>
          {isSpecialist && (
            <li>
              <Link to={'/users'}>Usuarios</Link>
            </li>
          )}
          {isClient && (
            <li>
              <Link to={'/pet'}>Mascotas</Link>
            </li>
          )}
          <li>
            <Link to={'/appointment'}>Citas</Link>
          </li>
          {isAdmin && ( // Mostrar el enlace solo si el usuario es un administrador
            <li>
              <Link to={'/breed'}>Razas</Link>
            </li>
          )}
          {isAdmin && ( // Mostrar el enlace solo si el usuario es un administrador
            <li>
              <Link to={'/animaltype'}>Tipos de animal</Link>
            </li>
          )}
        </ul>
      </div>
    </div>
  );
}
