import React from 'react';
//import { useState } from 'react';
import { BiPlus } from 'react-icons/bi';
import { useNavigate } from 'react-router-dom';
import BreedCard from '../BreedCard/breedCard';


export default function BreedList({ breedList }) {
  const navigate = useNavigate();
  const addUser = () => {
    navigate('./add');
  };
  return (
    <div className='content'>
      <div className='title'>Lista de razas</div>
      <div className='container container--user'>
      <div className='user-list-title'></div>
      {breedList && breedList.length > 0 ? (
        breedList.map((breedList) => (
          <BreedCard breed={breedList} key={breedList.id} />
        ))
      ) : (
        <p>No hay razas para mostrar</p>
      )}
    </div>
      <button className='btn btn--add-user' onClick={addUser}>
        <BiPlus fill='white' />
      </button>
    </div>
  );
}
