import React from 'react';
//import { useState } from 'react';

import { useNavigate } from 'react-router-dom';
import AppointmentCard from '../appointmentCard/appointmentCard';
import { BiPlus } from 'react-icons/bi';

export default function AppointmentList({ appointmentList }) {
  // const [userPerPage, setUserPerPage] = useState(15);
  const navigate = useNavigate();
  const addAppointment = () => {
    navigate('./add');
  };
  return (
    <div className='content'>
      <div className='title'>Lista de citas</div>
      <div className='container container--user'>
        <div className='user-list-title'></div>
        {appointmentList && appointmentList.length > 0 ? (
          appointmentList.map((appointment) => (
            <AppointmentCard appointment={appointment} key={appointment.id} />
          ))
        ) : (
          <p>No hay citas</p>
        )}
      </div>
      <button className='btn btn--add-user' onClick={addAppointment}>
        <BiPlus fill='white' />
      </button>
    </div>
  );
}
