import './App.css';
import { Routes, Route } from 'react-router';
import { HashRouter as Router, Navigate } from 'react-router-dom';
import Home from './components/home/Home';
import LoginPage from './components/login/LoginPage';
import Users from './components/user/Users';
import AddUser from './components/user/addUser/AddUser';
import UpdateUser from './components/user/updateUser/UpdateUser';
import UserDetailsCard from './components/user/userDetailCard/UserDetailsCard';
import AnimalType from './components/animalType/animalType';
import React from 'react';
import AddAnimalType from './components/animalType/addAnimalType/addAnimalType';
import Breed from './components/breed/breed';
import AddBreed from './components/breed/AddBreed/addBreed';
import UpdateAnimalType from './components/animalType/updateAnimalType/updateAnimalType';
import UpdateBreed from './components/breed/updateBreed/updateBreed';
import Pet from './components/pet/Pet';
import AddPet from './components/pet/addPet/AddPet';
import UpdatePet from './components/pet/updatePet/UpdatePet';
import Appointment from './components/appointment/Appointment';
import PetDetailCard from './components/pet/petDetailCard/PetDetailsCard';
import AddAppointment from './components/appointment/addAppointment/AddAppointment';

function App() {
  return (
    <Router>
      {' '}
      {/* Usa HashRouter en lugar de BrowserRouter */}
      <Routes>
        <Route path='*' element={<Navigate to='/home' replace />} />
        <Route path='/' element={<LoginPage />} />
        <Route path='/home' element={<Home />} />
        <Route path='/users' element={<Users />} />
        <Route path='/users/add' element={<AddUser />} />
        <Route path='/users/update' element={<UpdateUser />} />
        <Route path='/users/userDetails' element={<UserDetailsCard />} />
        <Route path='/animaltype' element={<AnimalType />} />
        <Route path='/animaltype/add' element={<AddAnimalType />} />
        <Route path='/animaltype/update' element={<UpdateAnimalType />} />
        <Route path='/breed' element={<Breed />} />
        <Route path='/breed/add' element={<AddBreed />} />
        <Route path='/breed/update' element={<UpdateBreed />} />
        <Route path='/pet' element={<Pet />} />
        <Route path='/pet/add' element={<AddPet />} />
        <Route path='/pet/update' element={<UpdatePet />} />
        <Route path='/pet/Details' element={<PetDetailCard />} />
        <Route path='/appointment' element={<Appointment />} />
        <Route path='/appointment/add' element={<AddAppointment />} />

        {/* <Route path="*" element={<NotFound />} /> */}
      </Routes>
    </Router>
  );
}

export default App;
